<template>
  <div>
    <el-menu

class="el-menu-demo"
mode="horizontal"

background-color="#409EFF"
text-color="#fff"
active-text-color="#ffd04b"
>
<el-menu-item  index="3" >
  <img style="height: 60px;"
src="../assets/jy.png"
/>

</el-menu-item>
<el-menu-item  >
  <span class="span">就业信息网</span>
</el-menu-item>
<el-menu-item style="float: right;" @click="changeUserInfo">
  <el-avatar size="medium " :src="user.avatar"></el-avatar>
</el-menu-item>
<el-menu-item style="float: right;" @click="logout">
  <img style="height: 40px;"
src="../assets/loyout.png"
/>
</el-menu-item>
</el-menu>

<el-form :inline="true" :model="formInline" class="demo-form-inline">
  <el-form-item label="职位名称">
    <el-input v-model="formInline.employName" placeholder="专业分类"></el-input>
  </el-form-item>
  <el-form-item label="学历">
    <el-select v-model="formInline.educational" placeholder="学历" clearable>
      <el-option label="专科" value="专科"></el-option>
      <el-option label="本科" value="本科"></el-option>
      <el-option label="研究生" value="研究生"></el-option>
      <el-option label="博士" value="博士"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="专业分类">
    <el-input v-model="formInline.professional" placeholder="专业分类"></el-input>
  </el-form-item>
  <el-form-item label="薪资">
    <el-select v-model="formInline.moneyType" placeholder="薪资" clearable="true">
      <el-option label="5K以下" value="5K以下"></el-option>
      <el-option label="5k-10k" value="5k-10k"></el-option>
      <el-option label="10K-20K" value="10K-20K"></el-option>
      <el-option label="20k-30k" value="20k-30k"></el-option>
      <el-option label="30k以上" value="30k以上"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="getList">查询</el-button>
  </el-form-item>
</el-form>

  <el-row >
    <el-col :span = 6 v-for= "item in list" :key="item.id" style="padding-right: 10px;padding-top: 10px; ">
      <el-card  style="height: 300px;">
        <div slot="header">
          <span  >{{ item.employName }}</span>
          <div v-if = "item.isShowTo" style="float: right; padding: 3px 0">已投递</div>
          <el-button @click="showToEmply(item.id)" v-if = "!item.isShowTo" style="float: right; padding: 3px 0" type="text">投递</el-button>
        </div>
        <el-tag size="mini">{{ item.educational }}</el-tag>
        <div> 薪资待遇 : {{ item.moneyType }}</div>
        <div> 专业分类 : {{ item.professional}}</div>
        <div> 简介 : <div style="font-size: 15px;
    color: #3031338c;">
          {{ item.employDes }}
        </div></div>
      </el-card>
    </el-col>
  </el-row>

  <el-dialog title="用户信息" :visible.sync="dialogFormVisible">
    <USER/>
  </el-dialog>
  </div>

  </template>

<script>
import { request } from '../reqeust'
import USER from './user.vue'
export default {
  components: {USER},
  data () {
    return {
      dialogFormVisible: false,
      data: {
        current: 1,
        size: 100
      },
      formInline: {
        current: 1,
        size: 100
      },
      list: [],
      user: JSON.parse(localStorage.getItem('user'))
    }
  },
  created () {
    console.log(this.user)
    request({
      url: '/employ/list',
      method: 'post',
      data: this.formInline
    }).then(res => {
      if (res.code === 0) {
        this.list = res.data.records
      }
    })
  },
  methods: {

    showToEmply (val) {
      console.log(val)
      var _this = this
      request({
        url: '/userEmploy/add',
        method: 'post',
        data: {
          'employId': val
        }
      }).then(res => {
        if (res.code === 0) {
          _this.$message({
            message: '投递成功',
            type: 'success'
          })
          _this.getList()
        }
      })
    },
    getList () {
      request({
        url: '/employ/list',
        method: 'post',
        data: this.formInline
      }).then(res => {
        if (res.code === 0) {
          this.list = res.data.records
        }
      })
    },
    changeUserInfo () {
      this.dialogFormVisible = true
      // this.$router.push('/user')
    },
    logout () {
      var _this = this
      request({
        url: '/user/logout',
        method: 'post',
        data: {}
      }).then(res => {
        if (res.code === 0) {
          localStorage.removeItem('user')
          _this.$router.push('/login')
        }
      })
    }
  }
}
</script>

  <style>
  .span {
    margin: 0 auto;
    margin-left: 30px;
    font-family: "Arial Negreta", "Arial Normal", Arial;
    font-weight: 700;
    font-style: normal;
    font-size: 20px;
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }

  </style>



